.complexity {
  margin-bottom: 30px;
}

.complexity:last-of-type {
  margin-bottom: 0;
}

.complexities {
  margin-top: 30px;
}

.complexity-caption{
  position: relative;
  border-bottom: 2px solid var(--border-color);
  color: var(--dark-text-color);
  margin-top: 0px;
}

.complexity-empty{
  margin: 0;
  text-transform: uppercase;
  text-align: center;
  padding: 40px 0px;
  background: var(--emphasize-color);
  border-radius: 5px;
}